import opg from 'ts/opg.ts';
import PopUp from 'ts/ui/Popup';
import {Languages} from 'ts/util/Languages';
import {Combo} from 'ts/ui/Combo';

const lpg = Languages.package;
console.log('Languages:', Languages);

opg.api({
	'addMain!post': 'Information/AddInfomationSubject',
	'upload!UPLOAD': 'upload/files',
});

let sponsorId;
const subjectPage = __uri('/pages/content/sponsor/addSubject.html');




Combo.makeClearableInput($('#logoPicture'), $({}));
let tdLogo = $('#td_logoPicture');
$('.ipt-eraser' , '#tdUploadLogo').on('click' , function () {
	tdLogo.empty();
});

Combo.makeClearableInput($('#coverPicture'), $({}));
let tdCover = $('#td_coverPicture');
$('.ipt-eraser' , '#tdUploadCover').on('click' , function () {
	tdCover.empty();
});

let uploadForm = $(`<form>
						<input name="file" type="file" accept="image/jpg,image/jpeg,image/png">
				</form>`);
$('#btnUploadDialog1,#btnUploadDialog2').click(function () {
	let target = $(this).data('target');
	(uploadForm[0] as HTMLFormElement).reset();
	let pop: PopUp = top.opg.confirm(uploadForm, function () {
		let formData = new FormData(uploadForm[0] as HTMLFormElement);
		opg.api.upload(formData, function (data) {
			let iptCoverPicture = $(`#${target}`);
			iptCoverPicture.val(data[0].downloadLink);

			let tdCover = $(`#td_${target}`);
			tdCover.html(`<img src="${data[0].downloadLink}" style="max-height: 100px;"/>`);

			pop.close();
		});
		return true;
	}, {
		title: `${lpg.upload}`,
		width: 200,
		height: 50,
		buttons: {
			ok: `${lpg.upload}`,
			cancel: `${lpg.cancel}`,
		},
	});


});


let tbMain = $('#tbMain');
let ulSubjects = $('#ulSubjects');
$('#btnSaveMain').click(function () {
	let param = tbMain.fieldsToJson({
		title: {
			name: lpg.sponsorTitle ,
			type: 'ns',
			require: true,
		},
	});

	if (param) {
		param.informationType = 2;

		opg.api.addMain(param, function (data) {
			sponsorId = data;

			$('input,button', tbMain).prop('disabled', true);


			$('#buttonSection').html(`
				<button id="btnAddSubject" class="btn-info">${lpg.addSubject}</button>
			`);

			$('#btnAddSubject').click(function () {
				let pop: PopUp = top.opg.confirm(`<iframe src="${subjectPage}?id=${sponsorId}" />`, function (i, ifr) {
					ifr.doSave(pop, ulSubjects);
					return true;
				}, {
					title: `${lpg.addSubject}`,
					width: 500,
					height: 450,
					buttons: {
						ok: `${lpg.save}`,
						cancel: `${lpg.cancel}`,
					},
				});
			});
		});
	}
});


